
@charset "UTF-8";
.app {
  position: absolute;
  font-family: '微软雅黑', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.nav-slide-enter-active {
  -webkit-animation: slide-enter .5s ease-in-out;
          animation: slide-enter .5s ease-in-out;
}
.nav-slide-leave-active {
  -webkit-animation: slide-leave .5s ease-in-out;
          animation: slide-leave .5s ease-in-out;
}
@-webkit-keyframes slide-enter {
0% {
    left: -420px;
}
100% {
    left: 0px;
}
}
@keyframes slide-enter {
0% {
    left: -420px;
}
100% {
    left: 0px;
}
}
@-webkit-keyframes slide-leave {
0% {
    left: 0px;
}
100% {
    left: -420px;
}
}
@keyframes slide-leave {
0% {
    left: 0px;
}
100% {
    left: -420px;
}
}
@media only screen and (min-width: 640px) {
#app {
    width: 640px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}
}

