
.ctn {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 60px;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.loading {
  position: absolute;
  width: 100%;
  font-size: 34px;
  line-height: 55px;
  padding-top: 400px;
}
.article {
  position: relative;
  padding: 4px 26px;
  text-align: left;
  font-size: 28px;
  line-height: 40px;
  word-break: break-all;
}
.article h1 {
    font-size: 40px;
    line-height: 50px;
}
.article h2 {
    font-size: 33px;
    line-height: 45px;
}
.article a, .article a:link, .article a:visited {
    color: #2c3e50;
    text-decoration: none;
}
.article .meta {
    display: inline-block;
    height: 38px;
    line-height: 38px;
    width: 588px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.article .meta img {
      vertical-align: middle;
      float: left;
}
.article .meta .author {
      font-weight: bold;
      margin-left: 8px;
}
.article .content {
    clear: both;
}
.article .content a, .article .content a:hover, .article .content a:visited, .article .content a:link, .article .content a:active {
      text-decoration: none;
      color: #1996d4;
      -webkit-tap-highlight-color: transparent;
}
.article .content p {
      margin: 20px 0;
}
.article .content iframe {
      width: 560px;
      height: 435.75px;
}
.article .content img {
      display: block;
      margin: 0 auto;
      width: auto;
      max-width: 100%;
}
.article .view-more {
    display: none;
    background-color: #ddd;
    text-align: center;
    height: 52px;
    line-height: 52px;
    margin-top: 19px;
}
.article .view-more a {
      display: block;
      font-size: 27px;
      width: 100%;
      height: 100%;
      text-decoration: none;
      color: #1919f3;
}



