
p {
  margin: 0;
}
.list {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.list .loading {
    position: absolute;
    width: 100%;
    font-size: 34px;
    padding-top: 400px;
}
.list .list-message {
    font-size: 0;
}
.list .list-message .data-editors {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-flow: row nowrap;
              flex-flow: row nowrap;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      line-height: 60px;
      height: 60px;
      font-size: 25px;
      text-align: left;
      padding: 0 43px 0 34px;
}
.list .list-message .data-editors:after {
        content: "";
        -webkit-box-flex: 0;
            -ms-flex: 0 0 15px;
                flex: 0 0 15px;
        height: 15px;
        border-top: 2px solid #6f6f6f;
        border-right: 2px solid #6f6f6f;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.list .list-message .data-editors span {
        font-size: 24px;
        color: #6f6f6f;
        -webkit-box-flex: 0;
            -ms-flex: 0 0 60px;
                flex: 0 0 60px;
}
.list .list-message .data-editors ul {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        margin: 0 30px;
        padding: 0;
}
.list .list-message .data-editors ul li {
          display: inline-block;
}
.list .list-message .data-editors ul li img {
            vertical-align: middle;
            height: 34px;
            margin: 8px 10px 12px;
            border-radius: 33px;
}
.list .list-message .date-mes {
      background-color: #5886e8;
      height: 58px;
      line-height: 58px;
      font-size: 28px;
      color: #fff;
      font-weight: bold;
}
.list .list-message ul.list-content {
      margin: 0 30px;
      padding: 0;
}
.list .list-message ul.list-content li {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        height: 136px;
        padding: 3px 0;
        -webkit-tap-highlight-color: transparent;
        border-top: 1px solid #ddd;
}
.list .list-message ul.list-content li p {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          text-align: justify;
          line-height: 24px;
          font-size: 23px;
          padding: 0 14px 0;
          font-weight: bold;
}
.list .list-message ul.list-content li img {
          width: 111px;
          overflow-y: hidden;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          vertical-align: middle;
}
.more-load {
  display: none;
  line-height: 70px;
  font-size: 23px;
  text-align: center;
  font-weight: bold;
  height: 70px;
  padding: 3px 0;
  border-top: 1px solid #ddd;
}
.dbload {
  display: block;
}
.top {
  position: relative;
  height: 350px;
}
.top .top-img {
    width: 100%;
    height: 350px;
    color: #000;
    background-color: #ddd;
    line-height: 440px;
    overflow: hidden;
}
.top .top-img img {
      width: 100%;
      margin-top: -145px;
}
.top .slide-content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0, 0, 0, 0.3);
}
.top .slide-content p {
      position: absolute;
      font-size: 29px;
      color: #fff;
      bottom: 23px;
      text-align: left;
      line-height: 44px;
      max-width: 600px;
      left: 50%;
      margin-left: -300px;
}



